body {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
    background-image: linear-gradient(90deg, transparent 0%, transparent 10%, rgb(255, 0, 21) 10%, rgb(255, 0, 21) calc(10% + 1px),
            transparent calc(10% + 1px), transparent 100%),
        linear-gradient(180deg, white 15%, transparent 15%),
        repeating-linear-gradient(180deg, #f2f2f2 0px, #f2f2f2 1px, transparent 1px, transparent 30px),
        linear-gradient(white, white);
    font-size: 1.6rem;
}

