.cover {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    background: #369;
    color: #fff;
    z-index: 1000;

    &.on {
        display: block;
    }
}

.header {
    position: fixed;
    font-family: 'Montserrat', sans-serif;
    top: 5rem;
    left: 2rem;
    font-size: 2rem;
    z-index: 1000;
}

.cover_open {
    position: fixed;
    top: 5rem;
    right: 5rem;
    font-size: 0;
    width: 4rem;
    height: 4rem;
    background: $mainColor;
    // background: $mwColor;
    border: none;
    // border: 2px solid $mainColor;
    border-radius: 0.5rem;
    z-index: 1000;

    &::before {
        content: "";
        position: absolute;
        top: 10px;
        left: 6px;
        right: 6px;
        height: 2px;
        border-radius: 2px;
        background: $mwColor;
        transition: 0.5s;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: 10px;
        left: 6px;
        right: 6px;
        height: 2px;
        border-radius: 2px;
        background: $mwColor;
        transition: 0.5s;
    }

    span {
        &::before {
            content: "";
            position: absolute;
            top: calc(50% - 1px);
            left: 6px;
            right: 6px;
            height: 2px;
            border-radius: 2px;
            background: $mwColor;
            transition: 0.5s;
        }

        &::after {
            content: "";
            position: absolute;
            top: calc(50% - 1px);
            left: 6px;
            right: 6px;
            height: 2px;
            border-radius: 2px;
            background: $mwColor;
            transition: 0.5s;
        }
    }
}

.cover_open.on {
    &::before {
        display: none;
    }

    &::after {
        display: none;
    }

    span {
        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }
    }
}

